$fcolor: #3f72af;
$scolor: #112d4e;

body {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  .container {
    background-color: #dbe2ef;
    width: 400px;
    border-radius: 30px;
    padding: 30px !important;
    height: 600px;
    h1 {
      color: $fcolor;
      font-family: "Pacifico", cursive;
    }
    form {
      display: flex;
      justify-content: space-between;
      input {
        border: unset;
        border-bottom: 2px solid $fcolor;
        background-color: inherit !important;
        padding: 5px;
      }
      input:focus {
        outline: unset;
        border-bottom: 2px solid $scolor;
      }
      .btn {
        background-color: $fcolor;
      }
      .btn:hover {
        background-color: $scolor;
      }
      .btn:focus {
        box-shadow: unset;
      }
    }
    li {
      color: $scolor;
      font-size: 25px;
      letter-spacing: 1.5px;
      font-family: "Odibee Sans", cursive;
      margin-bottom: 5px;
      text-transform: capitalize;
    }
  }
}
